// Перемешивает карты
function shuffle(array) {
    var _a;
    var currentIndex = array.length, randomIndex;
    while (currentIndex !== 0) {
        randomIndex = Math.floor(Math.random() * currentIndex);
        currentIndex--;
        _a = [
            array[randomIndex],
            array[currentIndex],
        ], array[currentIndex] = _a[0], array[randomIndex] = _a[1];
    }
    return array;
}
// Создаем массив из карт
export var createCardArray = function (level) {
    var cardArray = [];
    for (var index = 1; index < 37; index++) {
        cardArray.push("card-item".concat(index.toString()));
    }
    shuffle(cardArray);
    switch (level) {
        case '1':
            cardArray = cardArray.slice(0, 3);
            break;
        case '2':
            cardArray = cardArray.slice(0, 6);
            break;
        case '3':
            cardArray = cardArray.slice(0, 9);
            break;
        default:
            break;
    }
    // удваиваю количество карт
    cardArray = cardArray.concat(cardArray);
    shuffle(cardArray);
    return cardArray;
};
